@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-family:Roboto,Arial,sans-serif;font-size:14px}body{background:#f2f2f2}.grid{padding:50px;margin:0 auto;display:grid;width:90%;grid-template-columns:repeat(10, 1fr);grid-gap:10px 7px;gap:10px 7px}
.leaderboard{width:100%;border-spacing:0;counter-reset:place}.leaderboard__row .leaderboard__col:first-child::before{counter-increment:place;content:counter(place)}.leaderboard__row:first-child .leaderboard__col{padding-top:6px;border-top:solid 8px #fff}.leaderboard__col{padding:5px 10px}.leaderboard__col_header{padding-bottom:8px;border-bottom:solid 1px #000}.leaderboard__col_width_sm{width:30px}.leaderboard__col_text_right{text-align:right}.leaderboard__body .leaderboard__row:nth-child(1){background:gold}.leaderboard__body .leaderboard__row:nth-child(2){background:silver}.leaderboard__body .leaderboard__row:nth-child(3){background:#cd7f32}
.gamelevel__time,.gamelevel__name{display:inline-block}.gamelevel{display:-webkit-flex;display:flex}.gamelevel{-webkit-flex-direction:column;flex-direction:column;max-width:200px}.gamelevel__image{width:100%;opacity:.5}.gamelevel__image_active{opacity:1}.gamelevel__info{width:100%}.gamelevel__info_wrapp{padding-top:17px;padding-right:20px;padding-left:20px}.gamelevel__name{text-transform:capitalize;width:60%}.gamelevel__time{width:40%;text-align:right}.gamelevel__button{width:100%;padding:5px 0}.gamelevel__button_wrapp{padding:20px}
.button{cursor:pointer;border-radius:5px;outline:none;border:none;color:#fff;min-width:125px;padding:8px 5px;text-transform:uppercase;font-weight:700;font-size:14px;line-height:16px;text-align:center;letter-spacing:.1em;transition:all .2s}.button__primary{background-color:#70e5ff;box-shadow:0px 3px 0px #4fb7ce}.button__primary:hover{background-color:#a3eeff}.button__primary:active{-webkit-transform:translateY(0.5px);transform:translateY(0.5px);box-shadow:0px 2px 0px #4fb7ce;background-color:#0ad2ff}.button__secondary{background-color:#a6a6a6;box-shadow:0px 3px 0px #747474}.button__secondary:hover{background-color:silver}.button__secondary:active{-webkit-transform:translateY(0.5px);transform:translateY(0.5px);box-shadow:0px 2px 0px #747474;background-color:#737373}.button__danger{background-color:#ff4040;box-shadow:0px 3px 0px #b52424}.button__danger:hover{background-color:#ff7373}.button__danger:active{-webkit-transform:translateY(0.5px);transform:translateY(0.5px);box-shadow:0px 2px 0px #b52424;background-color:#d90000}.button__large{padding:12px 5px;background-color:#70e5ff;box-shadow:0px 5px 0px #4fb7ce;font-size:24px;line-height:26px;min-width:580px}.button__large:hover{background-color:#a3eeff}.button__large:active{-webkit-transform:translateY(2px);transform:translateY(2px);box-shadow:0px 2px 0px #4fb7ce;background-color:#0ad2ff}.button__box{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.button__checkbox{width:14px;height:14px;border:2px solid #fff;box-sizing:border-box;border-radius:2px;margin-right:8px;position:relative}.button__checkbox.active::before{content:"";border-radius:2px;-webkit-transform:rotate(45deg);transform:rotate(45deg);position:absolute;left:0;top:5px;height:2px;width:7px;background-color:#fff}.button__checkbox.active::after{content:"";border-radius:2px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);position:absolute;left:2px;bottom:8px;height:2px;width:20px;background-color:#fff}
.homepage{max-width:730px;margin:0 auto;padding:0 10px;display:grid;grid-gap:15px;gap:15px}.homepage__leaderboard-wrapper{padding:18px 60px 20px;background:#e3e3e3;box-shadow:0px 9px 0px rgba(0,0,0,.25);border-radius:0px 0px 50px 50px}.homepage__title{margin-bottom:30px;font-weight:900;font-size:48px;line-height:56px;text-align:center;text-transform:uppercase;color:#000}.homepage__level-wrapper{display:grid;grid-template-columns:1fr 1fr 1fr;justify-items:center}.homepage__subtitle{color:#6d6d6d;font-weight:700;font-size:24px;line-height:28px;text-align:center;letter-spacing:.1em}.homepage__button-wrapper{justify-self:center}
